.Container{
    display: flex;
    align-items: center;
    gap:1%;
    position: sticky;
    top: 0;
    height: 54px;
    z-index: 100;
    margin-bottom: 5px;
    padding: 0 7px;
    transition: all 0.5s ease;
    backdrop-filter: blur(10px);
}
.image{
    animation: rotation 3s infinite alternate;
}
@keyframes rotation {
    from {
      transform: scale(0.5);
    }
    to {
      transform: scale(1.1);
    }
  }

@media (max-width: 425px){
    .Container{
        justify-content: center;
    }
}
input[type="checkbox"] {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  z-index: 100;
  height: 54px;
}
input[type="checkbox"]:checked ~ div {
  color: yellow;
  text-shadow: 0 0 15px yellow, 0 0 25px yellow;
  animation: glow 1.5s linear infinite;
}

@keyframes glow {
  0% {
    filter: hue-rotate(0deg);
  }
  100% {
    filter: hue-rotate(360deg);
  }
}
.text{
  display: flex;
  font-size: 1.6rem;
}
li {
  list-style: none;
}